import {useEffect, useState} from "react";
import './test.css';
const hotPhotoList = () => {
    return  fetch("/api/photos", {
        method: "get",
    })
};

const getCatPhotos = ()=>{
    return  fetch('https://api.thecatapi.com/v1/images/search?limit=10').then((response)=>{return response.json()})
}

/*图片完全适应外层盒子居中显示*/
export const SuitBoxImage = ({url}) => {

    return (
        <div
            className='suit-box-image'
            style={{
                backgroundImage: `url(${url})`
            }}></div>
    );
};

export const Cats = (props)=>{
    const [catList,setCatList] = useState([]);
    useEffect(()=>{
        getCatPhotos().then(data=>{
            setCatList(data);
        })

    },[])
    return (<div>
        <div>
            <button onClick={()=>{
                setCatList([]);
                getCatPhotos().then(data=>{
                    setCatList(data);
                })
            }}

            >换一批</button>
        </div>

        <div className='cat-box'>
            {
                catList.map((item,index)=>{
                    return <div key={index} style={{margin:'10px',width:'240px',height:'240px',padding:'10px'}}><SuitBoxImage url={item.url}></SuitBoxImage></div>
                })
            }
        </div>
    </div>)
}

